    /////////////////////////////////////////////////////////////////////////////////
    //TODO:暂时未完成
    // | [enableCustomVideoCapture] | 启用视频自定义采集模式 |
    // | [sendCustomVideoData] | 向 SDK 投送自己采集的视频数据 |
